import React, {Component} from 'react';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';

import meetuplogo1 from '../images/meetup_logo.svg'
let meetuplogo = {};

class ResultCard extends Component {
    constructor(props) {
        super(props);

    }

    componentDidUpdate(){
        if(this.props.id == this.props.highlight) {
            ReactDOM.findDOMNode(this).scrollIntoView();
        }
    }

    render() {
        return (
            <div ref={"card_top"}>
                <br/>

                <div style={{'overflowY': 'auto'}}>
                    <div className="col-md-12">
                        <div className='card'>
                            <div className='card-header'
                                 style={this.props.id != this.props.highlight ? {'borderBottom': 'solid', 'borderColor': 'red'} : {'borderBottom': 'solid', 'background': 'limegreen'}}>
                                <h4>{this.props.type_event}</h4>
                            </div>
                            <div className='card-block' style={{'padding': '2px'}}>
                                <h4 className='card-title'> {this.props.meetup_name}</h4>
                                <p className='card-text'> {this.props.event_name} </p>
                                <a href={this.props.event_link} className='btn btn-primary' target="_blank">Go to event</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

ResultCard.propTypes = {};

export default ResultCard;



